/*# sourceMappingURL=bootstrap-grid.css.map */
:root {
    --main: #3382C5;
    --main-dark: #213C6C;
    --main-light: #45AAFF;
    --acsent: #F35653;
    --acsent-light: #F8F8F8;
}

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

body {
    font-family: 'Roboto', sans-serif;
    overflow-x: hidden;
    overflow-y: scroll;
}

img {
    width: 100%;
    height: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

form input,
form textarea {
    outline: none;
}

button {
    cursor: pointer;
}

textarea {
    resize: vertical;
}

.container-fluid {
    padding-left: 5%;
    padding-right: 5%;
}

.container-fluid>.row {
    margin-left: 0;
    margin-right: 0;
}

/* VENDOR */

.slick-dots li.slick-active button:before {
    background-color: #5CAFE2;
}

.slick-dots li button:before {
    content: '';
    background-color: gray;
    border-radius: 50%;
}

.slick-dots {
    bottom: -50px;
}
/* ELEMENTS */

.section-full {
    height: calc(100vh - 100px);
}

section:not(.section-full) {
    padding: 75px 0;
}

.section-dark {
    background-color: var(--main-dark);
}

.section-light {
    background-color: var(--main-light);
}

.section-acsent {
    background-color: var(--acsent);
}

.section-acsent-light {
    background-color: var(--acsent-light);
}

.section-dark:not(.section-side-left) p,
.section-light:not(.section-side-left) p,
.section-dark:not(.section-side-left) h1,
.section-light:not(.section-side-left) h1,
.section-dark:not(.section-side-left) h2,
.section-light:not(.section-side-left) h2,
.section-dark:not(.section-side-left) a,
.section-light:not(.section-side-left) a,
.section-dark:not(.section-side-left)  .section-title .section-name,
.section-dark:not(.section-side-left)  .section-title .section-description {
    color: white;
}

.section-side-left {
    background: -webkit-linear-gradient(left, var(--main) 25%, white 25%);
    background: linear-gradient(90deg, var(--main) 25%, white 25%)
}

.section-side-left.section-dark {
    background: -webkit-linear-gradient(left, var(--main-dark) 25%, white 25%);
    background: linear-gradient(90deg, var(--main-dark) 25%, white 25%)
}

.title-left {
    text-align: left;
}

.section-name {
    font-size: 16px;
    color: gray;
    font-weight: 300;
}

.section-description {
    font-size: 36px;
    margin-top: 0;
}

section p {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.5;
}

.section-title {
    position: relative;
    margin-bottom: 40px;
    padding-bottom: 20px;
}

.title-center {
    text-align: center;
}

.section-title:not(.section-title-simplified)::after {
    content: '';
    background-color: #F07F84;
    height: 1px;
    width: 100px;
    position: absolute;
    bottom: 0;
}

.section-dark:not(.section-side-left) .section-title::after {
    background-color: white;
}

.title-center.section-title::after {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.button {
    padding: 20px 40px;
    background: none;
    border: none;
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
    color: white;
    cursor: pointer;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
}

.button-acsent {
    background-color: var(--acsent);
    border: 1px solid var(--acsent);
}

.button-acsent:hover {
    background-color: white;
    color: black;
}

.button-light {
    background-color: var(--main-light);
}

.button-light:hover {
    background-color: var(--main);
}

.button-ghost {
    border: 1px solid white;
}

.button-ghost:hover {
    background-color: white;
    color: black;
}

.image-bordered img {
    border: 10px solid white;
    box-shadow: 0px 0px 100px rgba(30, 30, 31, 0.15);
}

.filter-item {
    font-size: 16px;
    border: none;
    color: black;
    padding: 10px 0;
    margin: 5px 15px;
    background: none;
}

.filter-item.active {
    border-bottom: 1px solid gray;
    color: gray;
}

.filter-item.hover {
    color: gray;
}

.filter-item:focus {
    outline: none;
    border-bottom: 1px solid gray;
}

/* SECTIONS */

/* HEADER */

header {
    height: 100px;
    overflow: hidden;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 8;
}

.header-brand {
    width: 255px;
    height: 100px;
}

.header-nav-item {
    padding: 30px 15px;
    font-size: 14px;
    font-weight: 300;
    color: gray;
    text-transform: uppercase;
    text-decoration: none;
}

.header-nav-item:hover {
    text-decoration: underline;
}

.header-controls {
    text-align: end;
}

.nav-mobile-wrapper {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    -webkit-transition: opacity 0.3s ease-in;
    transition: opacity 0.3s ease-in;
    opacity: 0;
    z-index: 10;
}

.nav-mobile {
    position: fixed;
    left: -30vw;
    top: 0;
    width: 30vw;
    background-color: rgb(37, 37, 37);
    height: 100vh;
    -webkit-transition: left 0.3s ease-out;
    transition: left 0.3s ease-out;
}

.header-nav-mobile-item {
    display: block;
    padding: 20px;
    background-color: rgb(53, 53, 53);
    color: white;
    text-decoration: none;
    border-bottom: 1px solid black;
}

.mobile-burger img {
    width: 35px;
    height: 35px;
}

/* BANNER */

#banner .banner-subtitle {
    text-align: center;
}

.banner-logo {
    width: 425px;
    height: 170px;
}

.banner-title {
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
}

.banner-controls {
    margin-top: 70px;
}

.banner-controls button {
    margin: 0 10px
}

/* ABOUT */

.about-image {
    width: 950px;
}

.about-image img {
    /* min-height: 523px; */
    object-fit: cover;
}

.progress-description {
    text-align: center;
}

.progress-step {
    position: relative;
}

.progress-step::after,
.progress-step::before {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.progress-step::before {
    content: '';
    position: absolute;
    bottom: -20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 36px;
    height: 6px;
    background-image: url(/wave.png);
}

.progress-step::after {
    content: '';
    position: absolute;
    top: 30%;
    right: -30%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 40px;
    background-image: url(/arrow-right.png);
}

.progress-step:last-child::after {
    display: none;
}

/* PUBLICATIONS */

.publication-item {
    background-size: cover;
    background-position: center;
    margin: 30px 15px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.publication-item .publication-content {
    height: 175px;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 30px 50px;
}

.publication-excerpt {
    color: white;
    font-size: 14px;
    line-height: 1.8;
    font-weight: 300;
}

.publication-link {
    color: white;
    text-decoration: none;
}

.publication-link:hover {
    border-bottom: 1px solid white;
}

.publication-compact {
    width: 370px;
    height: 320px;
}

.publication-large {
    width: 370px;
    height: 670px;
}

/* EMPLOYER MAP */

.map-privilege {
    margin: 15px;
}

.privilege-icon {
    width: 60px;
    height: 60px;
    background: #FFFFFF;
    border: 0.8px solid #0062A8;
    box-sizing: border-box;
    box-shadow: 0px 3.2px 16px rgba(0, 0, 0, 0.15);
    border-radius: 50%;
    padding: 15px;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.privilege-description {
    font-size: 11px;
    color: var(--main-dark);
    text-align: center;
}

.map-image {
    position: absolute;
    left: 0;
    top: 0;
    min-width: 300px;
    max-width: 800px;
    width: 50vw;
}

/* MANAGEMENT */

.management-card {
    width: 270px;
    height: 275px;
    margin: 12px;
    display: flex;
    align-items: flex-end;
    background-position: center;
    background-size: cover;
    position: relative;
}

.management-card:first-child {
    margin-left: 0;
}

.management-leader-name {
    font-size: 24px;
    margin: 0;
}

.management-leader-description {
    margin-top: 7vh;
    font-size: 18px;
    font-weight: 400;
}

.management-info {
    background: rgba(0, 0, 0, 0.5);
    height: 70px;
    width: 100%;
    text-align: center;
}

.management-info .management-name {
    font-size: 16px;
    color: white;
    font-weight: 300;
    margin-bottom: 5px;
}

.management-info .management-post {
    font-size: 14px;
    color: white;
    font-weight: 300;
    margin-top: 5px;
}

.management-leader-photo {
    margin-right: 24px;
}

/* ACHIEVES | REVIEWS */

.achieve-icon {
    width: 45px;
    height: 45px;
    object-fit: contain;
}

.achieve-number {
    font-size: 42px;
    font-weight: 400;
    margin: 0;
}

.achieve-description {
    font-size: 16px;
    color: gray;
    margin-top: 10px;
    text-align: center;
}

.achievement-item {
    padding: 50px 60px;
    border: 1px solid lightgray;
}

.author-photo {
    width: 83px;
    height: 83px;
    border-radius: 50%;
    margin-left: auto;
    margin-right: auto;
}

.author-name {
    color: white;
    font-size: 18px;
    font-weight: 300;
    text-align: center;
}

.author-occupation {
    color: white;
    font-size: 14px;
    font-weight: 300;
    text-align: center;
}

.review-text {
    text-align: center;
    font-size: 18px;
    line-height: 2;
}

.review-item {
    padding: 0 20%;
}

.author-social-item {
    margin: 5px;
}

.achievments-wrapper {
    margin-left: 0;
    margin-right: 0;
}

/* STEPS */

.step {
    background: #FFFFFF;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15);
    border-radius: 50px;
    padding: 10px 30px;
    width: 300px;
    height: 90px;
    margin: 0 80px;
    position: relative;
}

.step:last-child {
    margin-right: 0;
}

.step:first-child {
    margin-left: 0;
}

.step-content {
    color: #F35653;
    text-align: center;
    margin: 0;
}

.step:after {
    content: '';
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 40px;
    background-image: url(/arrow-right.png);
    position: absolute;
    top: 50%;
    right: -30%;
}

.step:last-child::after {
    display: none;
}

/* PAYMENT */

.payment-item {
    width: 350px;
    background: #FFFFFF;
    box-shadow: 0px 0px 50px rgba(92, 175, 226, 0.4);
}

.payment-header {
    border-bottom: 1px solid gray;
}

.payment-name {
    font-size: 18px;
    font-weight: 300;
    letter-spacing: 5.5px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 40px;
    margin-bottom: 10px;
}

.payment-price {
    font-size: 36px;
    font-weight: 400;
    text-align: center;
    margin-top: 10px;
}

.payment-price>span {
    color: lightgray;
    font-size: 14px;
}

.payment-content {
    padding: 50px;
    padding-top: 20px;
}

.payment-content-description {
    font-size: 14px;
    line-height: 2;
    color: gray;
    margin-bottom: 30px;
    text-align: center;
}

/* PARTNERS */

/* #partners .slick-initialized .slick-slide {
    display: flex;
    flex-wrap: wrap;
} */

.partner-item {
    width: 220px !important;
    height: 100px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.15);
    padding: 25px 15px;
    margin: 20px;
}

.partner-item > img {
    object-fit: contain;
}

/* CONTACTS */

.contact-input-row {
    margin-top: 15px;
    margin-bottom: 15px;
}

.contact-input-item,
.contact-input-text {
    width: 100%;
    border: 1px solid #ECECEC;
    padding: 15px;
}

/* ADRESS */
.adress-city {
    color: white;
    font-size: 36px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 300;
}

.adress-description {
    color: white;
    text-align: center;
    font-size: 14px;
    line-height: 2;
}

/* FOOTER */
footer {
    padding: 75px 0 20px 0;
}

.footer-title-wrapper {
    position: relative;
    margin-bottom: 50px;
}

.footer-title-wrapper::after {
    content: '';
    width: 42px;
    height: 4px;
    background-color: white;
    position: absolute;
    bottom: -25px;
    left: 0;
}

.footer-title {
    font-size: 24px;
    color: white;
}

.footer-links-item {
    margin: 15px 0;
}

.footer-links-item-link {
    color: darkgray;
    font-size: 14px;
    text-decoration: none;
    padding: 20px;
    padding-left: 0;
}

.footer-links-item:hover {
    text-decoration: underline;
}

.footer-letter-input-submit-button,
.footer-letter-input-field {
    background: none;
    border: none;
    padding: 0 0 10px 0;
}

.footer-letter-input-field {
    font-size: 14px;
    font-weight: 400;
    color: white;
}

.footer-letter-input-submit-button img {
    width: 20px;
    height: 15px;
    margin-left: auto;
    display: block;
}

.footer-letter-input {
    border-bottom: 2px solid white;
}

.footer-social-item {
    margin: 0 10px;
}

.footer-credits-text {
    color: #75757D;
    font-size: 14px;
}

.footer-credits-text span {
    color: white;
}

.footer-credits-text.contact {
    text-align: right;
}

/* XL */
@media(max-width: 1440px) {
    .progress-description {
        font-size: 14px;
    }

    .step {
        margin: 0 40px;
        width: 200px;
    }

    .step:after {
        right: -25%;
    }
}

/* LG */
@media(max-width: 1200px) {
    .publication-item {
        min-height: 320px;
        padding: 0;
    }

    .publication-item .publication-content {
        padding: 0 50px;
    }
}

/* MD */
@media(max-width: 992px) {
    .section-side-left.section-dark {
        background: -webkit-linear-gradient(top, var(--main-dark) 45%, white 45%);
        background: linear-gradient(to bottom, var(--main-dark) 45%, white 45%)
    }
    .section-side-left {
        background: -webkit-linear-gradient(top, var(--main) 45%, white 45%);
        background: linear-gradient(to bottom, var(--main) 45%, white 45%)
    }

    .progress-step {
        margin: 30px 0;
    }

    .progress-step::after {
        display: none;
    }
 
    .map-image {
        position: relative;
    }
    .step {
        margin: 20px 0;
    }

    .step:after {
        display: none;
    }
    .footer-credits-text,
    .footer-credits-text.contact {
        text-align: center;
    }
}

/* SM */
@media(max-width: 568px) {
    .section-side-left {
        background: -webkit-linear-gradient(top, var(--main) 15%, white 15%);
        background: linear-gradient(to bottom, var(--main) 15%, white 15%)
    }
    .section-side-left.section-dark {
        background: -webkit-linear-gradient(top, var(--main-dark) 15%, white 15%);
        background: linear-gradient(to bottom, var(--main-dark) 15%, white 15%)
    }
    .banner-logo {
        width: 100%;
        height: auto;
    }

    .section-content {
        padding-left: 10px !important;
        padding-right: 10px !important;
    }

    .nav-mobile {
        width: 45vw;
    }
    
    .management-leader-photo {
        margin-right: 0;
    }

    .review-item {
        padding: 0;
    }

    .contact-input-row input {
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .button {
        padding: 20px 30px;
    }

    .publication-item .publication-content {
        padding: 0 10px;
    }
    .achieve-number {
        font-size: 32px;
    }

    .payment-item {
        width: 280px;
    }
}
